body * {
  box-sizing: border-box;
}

h1, h2, h3, h4, p, div, body, a, button, ul, li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  width: 280px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (width >= 768px) and (width <= 1199px) {
  .container {
    width: 700px;
  }
}

@media (width >= 1199px) {
  .container {
    width: 1119px;
  }
}

